function removeSelectedMaker() {
  var icons = document.getElementsByClassName("leaflet-marker-icon");
  for (var i = 0; i < icons.length; i++) {
    if (icons.hasOwnProperty(i)) {
      if (
        typeof icons[i].src != "undefined" &&
        icons[i].src.indexOf("special") !== -1
      ) {
        icons[i].src = files + "images/map-point-special.png";
      } else {
        icons[i].src = files + "images/map-point.png";
      }
    }
  }
}

function restoreMapSettings() {
  if (typeof window.mapBoxSettingsSave == "object") {
    window.mapboxMode = "listing";
    window.mapboxMap.flyTo(
      window.mapBoxSettingsSave.center,
      window.mapBoxSettingsSave.zoom
    );
  }
}

function initOpenStreetMap(element) {
  if (document.getElementById("js-page-view")) {
    var links = document.getElementById("js-page-view").querySelectorAll("a");
    for (var i = 0; i < links.length; i++) {
      if (links.hasOwnProperty(i)) {
        links[i].addEventListener("click", function () {
          setTimeout(function () {
            window.dispatchEvent(new Event("resize"));
          }, 500);
        });
      }
    }
  }

  var markers = window.getMarkers()["features"];

  var map = L.map(element.id).setView([51.505, -0.09], 13);

  L.tileLayer("https://a.tile.openstreetmap.org/{z}/{x}/{y}.png", {
    attribution:
      'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery  <a href="https://www.mapbox.com/">Mapbox</a>',
    maxZoom: 18,
    id: "mapbox.streets",
  }).addTo(map);

  window.mapboxMap = map;
  window.mapboxMode = element.getAttribute("data-mode")
    ? element.getAttribute("data-mode")
    : "listing";

  var markersGroup = L.markerClusterGroup({
    iconCreateFunction: function (cluster) {
      var childCount = cluster.getChildCount();
      return new L.DivIcon({
        html: "<div><span>" + childCount + "</span></div>",
        className: "marker-cluster",
        iconSize: new L.Point(40, 40),
      });
    },
  });
  window.solvedMarkers = [];
  var bounds = null;
  for (var i = 0; i < markers.length; i++) {
    if (
      markers[i]["geometry"]["coordinates"][0] < 90 &&
      markers[i]["geometry"]["coordinates"][1] < 90
    ) {
      if (bounds) {
        bounds.extend(
          L.latLng(
            markers[i]["geometry"]["coordinates"][1],
            markers[i]["geometry"]["coordinates"][0]
          )
        );
      } else {
        var corner1 = L.latLng(
          markers[i]["geometry"]["coordinates"][1],
          markers[i]["geometry"]["coordinates"][0]
        );
        var corner2 = L.latLng(
          markers[i]["geometry"]["coordinates"][1],
          markers[i]["geometry"]["coordinates"][0]
        );
        bounds = L.latLngBounds(corner1, corner2);
      }

      var marker = L.marker(
        [
          markers[i]["geometry"]["coordinates"][1],
          markers[i]["geometry"]["coordinates"][0],
        ],
        {
          icon: createMarkerElement(
            markers[i]["properties"]["advert_id"],
            markers.length !== 1,
            markers[i]["properties"]["show_marker"]
          ),
        }
      );
      marker.advert_id = markers[i]["properties"]["advert_id"];

      if (markers.length > 1) {
        if (markers[i].properties.link.length > 0) {
          marker.bindPopup(
            "" +
              '<a href="' +
              markers[i].properties.link +
              '">' +
              '<div class="estate estate--simple">' +
              '<div class="estate__image">' +
              '<img loading="lazy" src="' +
              markers[i].properties.image +
              '" alt="">' +
              "</div>" +
              '<div class="estate__content">' +
              '<h3 class="title title--sm estate__title">' +
              markers[i].properties.textTop +
              "</h3>" +
              '<p class="title title--next estate__price"><small>' +
              markers[i].properties.textBottom +
              "</small></p>" +
              "</div>" +
              "</div>" +
              "</a>"
          );
        } else {
          marker.bindPopup(
            "" +
              '<div class="estate estate--simple">' +
              '<div class="estate__image">' +
              '<img loading="lazy" src="' +
              markers[i].properties.image +
              '" alt="">' +
              "</div>" +
              '<div class="estate__content">' +
              '<h3 class="title title--sm estate__title">' +
              markers[i].properties.textTop +
              "</h3>" +
              '<p class="title title--next estate__price"><small>' +
              markers[i].properties.textBottom +
              "</small></p>" +
              "</div>" +
              "</div>"
          );
        }

        marker.on("mouseover", function (e) {
          this.openPopup();
        });
        marker.on("mouseout", function (e) {
          this.closePopup();
        });
      }

      window.solvedMarkers.push(marker);

      marker.addEventListener("click", function (e) {
        if (window.mapboxMode === "listing") {
          window.mapBoxSettingsSave = {
            center: window.mapboxMap.getCenter(),
            zoom: window.mapboxMap.getZoom(),
          };
        }
        // window.mapboxMode = "detail";
        // loadPropertyToModal(e.sourceTarget.advert_id);
        // removeSelectedMaker();
        console.log(e.sourceTarget);

        if (!e.sourceTarget.show_marker) {
          e.originalEvent.target.src = files + "img/icons/icon-location.svg";
        } else if (e.sourceTarget.advert_id == 3765660) {
          e.originalEvent.target.src = files + "img/icons/icon-location.svg";
        } else {
          e.originalEvent.target.src = files + "img/icons/icon-location.svg";
        }

        window.mapboxMap.flyTo(e.latlng, 17);
      });

      markersGroup.addLayer(marker);
    }
  }

  map.addLayer(markersGroup);
  map.fitBounds(markersGroup.getBounds());
  // map.setZoom(11);
}
